import axios from '@axios'

export default {
  namespaced: true,
  state: {
    formulario: [{ secciones: [] }],
    preguntaFocus: { a: '' },
  },
  getters: {},
  mutations: {},
  actions: {

    getFormulario(ctx, idFormulario) {
      return new Promise((resolve, reject) => {
        axios
          .get(`/recursos/formulario/${idFormulario}`)
          .then(response => resolve(response))
          .catch(error => reject(error))
      })
    },

    updateFormulario(ctx, formulario) {
      return new Promise((resolve, reject) => {
        axios
          .post('/recursos/formulario', formulario)
          .then(response => resolve(response))
          .catch(error => reject(error))
      })
    },
    // postRespuesta(ctx, params) {
    //   return new Promise((resolve, reject) => {
    //     axios
    //       .post(`/recursos/formulario/${ctx.state.formularioId}/respuesta`, params)
    //       .then(response => resolve(response))
    //       .catch(error => reject(error))
    //   })
    // },

  },
}
