<template>
  <div class="">
    <b-card>
      <b-row>
        <b-col
          lg="12"
          md="12"
          sm="12"
          class="d-flex justify-content-start mb-1"
        >
          <!-- <h3><strong>NOMBRE DEL FORMULARIO:</strong></h3> -->
          <b-form-input
            v-model="formulario[0].nombre"
            size="lg"
            class="font-weight-bold"
            style="background-color:#EFFBF8; color:#0B614B; border-top: 0px; border-bottom: 2px solid #048975; border-left: 0px; border-right: 0px; border-radius:0px "
          />
        </b-col>
        <!-- <b-col
          lg="4"
          md="4"
          sm="12"
        >
          <label
            class="text-primary"
          >Datos guardados</label>
        </b-col> -->
        <b-col cols="12">
          <b-form-textarea
            v-model="formulario[0].descripcion"
            placeholder="Sin descripción ..."
            rows="2"
          />
        </b-col>
      </b-row>
    </b-card>
    <!-- <b-nav class="wrap-border">
      <b-nav-item active>
        Preguntas
      </b-nav-item>
      <b-nav-item>Link</b-nav-item>
      <b-nav-item>Another Link</b-nav-item>
      <b-nav-item disabled>
        Disabled
      </b-nav-item>

    </b-nav> -->
    <br>
    <b-tabs
      content-class="mt-2"
      justified
      align="center"
      class="classTab"
    >
      <b-tab
        title="Preguntas"
        active
        class="pb-2"
      >
        <!-- <pre>{{ formulario }}</pre> -->
        <b-row
          v-for="(item, indexSeccion) in formulario[0].secciones"
          :key="indexSeccion"
        >
          <b-col cols="12">
            <new-seccion
              :index-seccion="indexSeccion"
            />
          </b-col>
        </b-row>
      </b-tab>
      <b-tab
        title="Respuestas"
      >
        <b-card-text>
          Por trabajar respuestas
        </b-card-text>
        <pre>{{ formulario }}</pre>

      </b-tab>
      <b-tab title="Configuración">
        <b-card-text>
          Por trabajar
        </b-card-text>

      </b-tab>

    </b-tabs>
  </div>
</template>

<script>import {
  BTabs, BTab, BCardText, BRow, BCol, BFormInput, BCard, BFormTextarea,
} from 'bootstrap-vue'
import { onUnmounted } from '@vue/composition-api'
// import { heightTransition } from '@core/mixins/ui/transition'
import Ripple from 'vue-ripple-directive'
import store from '@/store'
import { mapState } from 'vuex'

import NewSeccion from './newSeccion.vue'
import newFormularioStoreModule from './newFormularioStoreModule'

export default {
  components: {
    // BButton,
    BCard,
    BFormTextarea,
    BTabs,
    BTab,
    BRow,
    BCol,
    BFormInput,
    BCardText,

    // extern
    NewSeccion,

  },
  directives: {
    Ripple,
  },

  // mixins: [heightTransition],
  data() {
    return {

    }
  },
  computed: {
    ...mapState('newFormularioModule', ['formulario']),
  },
  created() {
    store.dispatch('newFormularioModule/getFormulario', this.$route.params.id_formulario).then(res => {
      console.log('FORMULARIO INICIO:', res)
      this.formulario.splice(0, 1)
      // this.formulario = res.data

      this.formulario.push(res.data)
    })
  },
  mounted() {

  },
  // created() {
  //   window.addEventListener('resize', this.initTrHeight)
  // },
  // destroyed() {
  //   window.removeEventListener('resize', this.initTrHeight)
  // },
  methods: {
    // activarBCard() {
    //   this.herramientaActiva = !this.herramientaActiva
    // },
    actualizarFormulario() {
      store.dispatch('newFormularioModule/updateFormulario', this.formulario).then(res => {
        console.log('¡FORMULARIO ACTUALIZADO!')
        console.log('DATA:', res)
        this.$set(this.formulario, 0, res.data)
        // this.formulario[0] = res.data
      })
    },
    // Metodos de vuexy para la transition
    repeateAgain() {
      this.items.push({
        id: this.nextTodoId += this.nextTodoId,
      })

      // this.$nextTick(() => {
      //   this.trAddHeight(this.$refs.row[0].offsetHeight)
      // })
    },
    // removeItem(index) {
    //   this.items.splice(index, 1)
    //   this.trTrimHeight(this.$refs.row[0].offsetHeight)
    // },
    // initTrHeight() {
    //   this.trSetHeight(null)
    //   this.$nextTick(() => {
    //     this.trSetHeight(this.$refs.form.scrollHeight)
    //   })
    // },
  },

  setup() {
    const NEW_FORMULARIO_STORE_MODULE_NAME = 'newFormularioModule'

    // Register module
    if (!store.hasModule(NEW_FORMULARIO_STORE_MODULE_NAME)) store.registerModule(NEW_FORMULARIO_STORE_MODULE_NAME, newFormularioStoreModule)

    // UnRegister on leave
    onUnmounted(() => {
      if (store.hasModule(NEW_FORMULARIO_STORE_MODULE_NAME)) store.unregisterModule(NEW_FORMULARIO_STORE_MODULE_NAME)
    })
    // console.log('RUTAS:', this.$route)
    // store.state.newFormularioModule.formularioId = router.currentRoute.params.id // formulario seleccionado
    // formulario seleccionado
    return {
      newFormularioStoreModule,
    }
  },
}
</script>

<style lang="scss" scoped>
.repeater-form {
  overflow: hidden;
  transition: .35s height;
}
